import React from 'react'
import { Badge, Button, Card, Col, Container, Form, Row } from 'react-bootstrap'
import qrCode from '../../assets/qrcodes/adiNathMiniQr.png'

const Index = () => {
  const [amount, setAmount] = React.useState('')
  const [transactionId, setTransactionId] = React.useState('')

  return (
    <Container fluid className="d-flex mb-3">
      <Container className="col-12 col-md-4">
        <Card>
          <Card.Header>Add to wallet</Card.Header>
          <Card.Body className="d-flex justify-content-center">
            <div className="">
              <div>
                <div className="">
                  <img
                    src={qrCode}
                    style={{
                      width: '100%',
                      height: '100%',
                      objectFit: 'contain',
                    }}
                    alt="qrcode"
                  />
                </div>
              </div>
              <div className="d-flex align-items-center justify-content-center mt-3">
                    <Button type="button" className="bg-black text-white myCustomHover mx-1" onClick={() => setAmount(100)}>
                      100
                    </Button>
                    <Button type="button" className="bg-black text-white myCustomHover mx-1" onClick={() => setAmount(200)}>
                      200
                    </Button>
                    <Button type="button" className="bg-black text-white myCustomHover mx-1" onClick={() => setAmount(500)}>
                      500
                    </Button>
                    <Button type="button" className="bg-black text-white myCustomHover mx-1" onClick={() => setAmount(1000)}>
                      1000
                    </Button>
              </div>
              <div className="">
                <Form>
                  <Row className="mb-3">
                    <Form.Group as={Col} className="col-12" controlId="formName">
                      <Form.Label>
                        <b>Amount </b>
                      </Form.Label>
                      <Form.Control
                        type="text"
                        placeholder="Enter Amount"
                        onChange={(e) => {
                          setAmount(e.target.value)
                        }}
                        value={amount}
                        required
                      />
                    </Form.Group>
                  </Row>
                  <Row className="mb-3">
                    <Form.Group as={Col} className="col-12" controlId="formName">
                      <Form.Label>
                        <b>Transaction ID</b>
                      </Form.Label>
                      <Form.Control
                        type="text"
                        placeholder="Enter Transaction ID"
                        onChange={(e) => {
                          setTransactionId(e.target.value)
                        }}
                        value={transactionId}
                        required
                      />
                    </Form.Group>
                  </Row>
                  <Row>
                    <Button className='myCustomBtn'>
                      Add to wallet
                    </Button>
                  </Row>
                </Form>
              </div>
            </div>
          </Card.Body>
        </Card>
      </Container>
      <Container className="col-12 col-md-7">
        <Card>
          <Card.Header>Add to wallet history</Card.Header>
          <Container>
            <table className="w-100">
              <tr>
                <th>SN</th>
                <th>Date</th>
                <th>Transaction ID</th>
                <th>Amount</th>
                <th>Revert Reason</th>
                <th>Status</th>
              </tr>
              <tr>
                <td>1</td>
                <td>12:04:45</td>
                <td>transactionid</td>
                <td>200</td>
                <td> Reason</td>
                <td>
                  <Badge className="bg-warning text-black">pending</Badge>
                </td>
              </tr>
            </table>
          </Container>
        </Card>
      </Container>
    </Container>
  )
}

export default Index
